@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rambla&display=swap");
.home_title_wrapper {
  position: absolute;
  left: 0;
  top: 5%;
  bottom: 20%;
  font-size: 50px;
  text-align: center;
  font-family: "Righteous", cursive;
  border-style: solid;
  border-width: 100px;
  color: #ffff;
  margin-right: 100px;
  margin-left: 100px;
  height: 696px;
  z-index: 0;
  background-image: url("../../images/kevin-mueller-1gUES9J7Ph8-unsplash.jpg");
  background-position: center;
  background-size: contain;
}
.home_button {
  font-family: "Rambla", sans-serif;
  font-size: 20px;
  padding: 30px;
  color:rgb(0, 0, 0);
  background: rgba(255, 255, 255, 0.747);
}
p {
  opacity: 100%;
}
.home_links {
  color: black;
  font-size: 20px;
  font-family: "Rambla", sans-serif;
  margin-top: 100px;
  a {
    margin: 20px;
    text-decoration: none;
    color: black;
  }
  a:hover {
      text-decoration: underline;
  }
}
.title_holder {
  h1 {
    display: block;
    margin-left: 0;
    margin-right: 0;
    font-weight: bold;
  }
}
